<template>
    <div class="distributeTable" id="distributeTable">
        <table class="mytable" id="tableCheck">
            <tr :style="{
                  'background' : $root.themeHomeChange === '1' ? '#fff' : ''
                }"
                :class="$root.themeHomeChange === '1' ? 'th_light' : 'th'">
                <th class="soft">排序</th>
                <th class="part">层级</th>
                <th class="num">告警数</th>
                <th class="xy">整改率</th>
            </tr>
            <tr :style="{
                  'color' : $root.themeHomeChange === '1' ? '#585858' : '#fff',
                  'background-image' : $root.themeHomeChange === '1' ? 'linear-gradient(90deg, rgba(67,137,249,0.06) 0%, rgba(67,137,249,0.20) 49%, rgba(67,137,249,0.06) 100%)' : '',
                }"
                v-for="(item,index) in tableList" :key="index">
                <td>{{index+1}}</td>
                <td>{{item.part}}</td>
                <td style="color: #FFC800">{{item.num}}</td>
                <td style="font-weight: bold;">{{item.xy}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
  export default {
    name: "distributeTable",
    data() {
      return {
        tableList:[
          {
            part:'办公室',
            num:'15',
            xy:'99%',
          },
          {
            part:'市场营销部',
            num:'40',
            xy:'95%',
          },
          {
            part:'资产管理部',
            num:'20',
            xy:'98%',
          },
          {
            part:'安全监管部',
            num:'22',
            xy:'97%',
          },
          {
            part:'横栏供电分局',
            num:'10',
            xy:'100%',
          },
          {
            part:'神湾供电分局',
            num:'8',
            xy:'99%',
          },
          {
            part:'大涌供电分局',
            num:'9',
            xy:'98%',
          },
          {
            part:'东升供电分局',
            num:'10',
            xy:'99%',
          },
          {
            part:'古镇供电分局计量运维班',
            num:'5',
            xy:'100%',
          },
          {
            part:'坦洲供电分局客户服务班',
            num:'4',
            xy:'100%',
          },
          {
            part:'三乡供电分局配电运维班',
            num:'5',
            xy:'100%',
          },
        ],
        timer: ''
      }
    },
    mounted() {
      this.init();
      let tableDom = document.getElementById('tableCheck');
      tableDom.onmouseenter =  () => {
        this.pause();
      }
      tableDom.onmouseleave =  () => {
        this.init();
      }
    },
    methods: {
      init() {
        clearInterval(this.timer);
        document.getElementById('distributeTable').scrollTop = 0;
        this.tableMove()
        this.timer = setInterval(() =>{
          this.tableMove()
        },1500)
      },
      pause(){
        clearInterval(this.timer);
      },
      tableMove() {
        // 48px 为表格行高
        setTimeout(() =>{
          this.tableList.splice(0, 1);
        }, 1200)
        setTimeout(() =>{
          this.tableList.push(this.tableList[0]);
        }, 1500)
      }
    },
    beforeDestroy() {
      this.pause();
    },
    watch: {
      $route(v) {
        if(v.path !== '/riskControlCenter' && v.path !== '/overView' ) {
          this.pause();
        }
      }
    }
  }
</script>

<style scoped lang="scss">
    .distributeTable {
        height: calc(100% - 10px);
        overflow: auto;
        box-sizing: border-box;
        padding: 0px 12px 6px 12px;
        transition: all .5s;
        .mytable {
            border: 1px solid #A6C1E4;
            font-family: Arial;
            border: none;
            border-collapse: separate;
            border-spacing: 0px 8px;
            width: 100%;
        }
        table{
            .th {
              th {
                  /*background-color: #71c1fb;*/
                  height: 20px;
                  font-size: 12px;
                  border: none;
                  color: #00F8FF;
                  font-weight: 700;
                  padding: 4px;
                  border: none;
              }
            }
            .th_light {
              th {
                  /*background-color: #71c1fb;*/
                  height: 20px;
                  font-size: 12px;
                  border: none;
                  color: #4389F9;
                  font-weight: 700;
                  padding: 4px;
                  border: none;
              }
            }
            td {
                height: 12px;
                font-size: 12px;
                padding: 4px 6px;
                border: none;
            }
            tr{
                color: #FFF;
                width: 100%;
                text-align: left;
                height: 32px;
                background-image: linear-gradient(90deg, rgba(0,73,178,0.00) 0%, #1A4C94 71%, rgba(0,73,178,0.00) 100%);
                margin-top: 10px;
                .soft{
                    width: 10%;
                }
                .part{
                    width: 50%;
                }
                .num{
                    width: 20%;
                }
                .xy{
                    width: 20%;
                }
            }
        }
    }

</style>